.todo__btn {
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: 100%;

  border: 0;
  cursor: pointer;
  flex-shrink: 0;

  width: 23px;
  height: 23px;
  padding: 0;
  margin: 0;
  margin-right: 30px;
}

.todo__btn:last-child {
  margin-right: 0;
}
